import React, { Component } from 'react'
import PlayButton from './PlayButton'
import FruitContainer from './FruitContainer'
import TopInfo from './TopInfo'

// let fruits = ['apple', 'banana', 'blueberry', 'cherry', 'lemon', 'pear', 'star', 'strawberry']
let dummyArray = [[],[],[]]
let fruitsLength = 20
let odds = [0.14, 0.16, 0.12, 0.2, 0.1, 0.08, 0.03, 0.17]
//let wins = [16, 12, 20, 4, 40, 60, 100, 8]
let wins
let twoSameCut
let myInterval

class Logic extends Component {

    constructor(props) {
        super(props)
      
        this.state = {
            play: false,
            fruitsCol: [[],[],[]],
            money: 10,
            lastWin: 0
            //wins: props.wins,
            //twoSameCut: props.twoSameCut
        }

        wins = props.wins
        twoSameCut = props.twoSameCut

        this.rollfruits = this.rollfruits.bind(this)
    }

    componentDidMount() {
        this.setupFruitsArray()
        console.log('mounted')
    }

    getFruitIndex() {
        var rnd = Math.random()
        var cumul = 0

        for (let index = 0; index < odds.length; index++) {
            cumul += odds[index] 
            if (rnd <= cumul) return index
        }

        return odds.length-1
    }

    setupFruitsArray() {

        for (let index = 0; index < fruitsLength; index++) {
            for (let col = 0; col < 3; col++) {

                dummyArray[col].push(this.getFruitIndex())

            }
            // fruitsColOne.push(Math.floor(Math.random() * 8))
            // fruitsColTwo.push(Math.floor(Math.random() * 8))
            // fruitsColThree.push(Math.floor(Math.random() * 8))
        }

        this.updateArrayState()
        
    }
/*
    setRandomFruits() {
        for (let index = 0; index < this.state.fruitsCol[0].length; index++) {
            for (let col = 0; col < 3; col++) {
                dummyArray[col][index] = (Math.floor(Math.random() * 8))
            }
            // fruitsColOne[index] = (Math.floor(Math.random() * 8))
            // fruitsColTwo[index] = (Math.floor(Math.random() * 8))
            // fruitsColThree[index] = (Math.floor(Math.random() * 8))
        }

        this.updateArrayState()
    }
*/
    handlePlay = (playState) => {

        console.log('handlePlay')

        this.setState({
            play: playState,
            money: this.state.money - 1
        })

        if (playState) {
            myInterval = setInterval(this.rollfruits, 400)
        }
    }

    rollfruits() {
        if (dummyArray[0].length > 3) {
            for (let col = 0; col < 3; col++) {
                dummyArray[col].shift()
            }

            this.updateArrayState()
        }
        else {
            this.calculateWinnings()
            clearInterval(myInterval)
        }
    }

    updateArrayState() {
        
        this.setState({
            fruitsCol: dummyArray
        })
        
    }

    calculateWinnings() {
        var moneyWon = 0

        if(this.state.fruitsCol[0][1] === this.state.fruitsCol[1][1] && 
            this.state.fruitsCol[1][1] === this.state.fruitsCol[2][1]) {
            console.log('kolme oikein')
            moneyWon = wins[this.state.fruitsCol[0][1]]
        }
        else if (this.state.fruitsCol[0][1] === this.state.fruitsCol[1][1]) {
            console.log('kaksi oikein')
            moneyWon = wins[this.state.fruitsCol[0][1]] / twoSameCut
        }
        else {
            console.log('ei voittoa')
        }

        console.log('rahaa voitettiin: ' +moneyWon)

        var newPlayState = false;

        if (this.state.money <= 0 && moneyWon === 0) {
            newPlayState = true;
        }

        this.setState({
            play: newPlayState,
            money: this.state.money + moneyWon,
            lastWin: moneyWon
        })

        this.setupFruitsArray()
    }

    

    render() {

        console.log(this.state.fruitsCol)


        return (
            <div>
                <TopInfo money={this.state.money} lastWin={this.state.lastWin}/>

                <FruitContainer playState={this.state.play} 
                colOne={this.state.fruitsCol[0]} 
                colTwo={this.state.fruitsCol[1]}
                colThree={this.state.fruitsCol[2]}
                />

                <PlayButton onPressPlay={this.handlePlay} playState={this.state.play} />

            </div>
        )
    }

    componentDidUpdate() {
        // console.log(this.state.play)
        //console.log(this.state.fruitsCol);
    }
}

export default Logic
