import React, { Component } from 'react'
import FruitColumn from './FruitColumn'

class FruitContainer extends Component {
    render() {
        console.log('container renderöityy')

        return (
            <div className='Fruit-container' width='192' height='192'>
                <FruitColumn update={this.props.playState} myFruits={this.props.colOne}/>
                <FruitColumn update={this.props.playState} myFruits={this.props.colTwo}/>
                <FruitColumn update={this.props.playState} myFruits={this.props.colThree}/>
            </div>
        )
    }
}

export default FruitContainer
