import React, { Component } from 'react'
import Fruit from './Fruit'

class FruitColumn extends Component {
    render() {

        if (this.props.update) {
            // console.log('uudet hedelmät')
        }

        return (
            <div className='Fruit-column' width='64' height='192'>
                <Fruit newFruit={this.props.myFruits[2]} styleClass='Fruit' size='64'/>
                <Fruit newFruit={this.props.myFruits[1]} styleClass='Fruit-middle' size='64'/>
                <Fruit newFruit={this.props.myFruits[0]} styleClass='Fruit' size='64'/>
            </div>
        )
    }

    componentDidUpdate() {
        // console.log('columnit päivittyy')
    }
}

export default FruitColumn
